import {Entity} from "./entity.model";

export class Track extends Entity {
    name: string;
    nameUnsign: string;
    length: number;
    playTime: number;
    downloadTime: number;
    username: string;
    artist: string;
    path: string;
    bitrate: string;
    picture: string;
    createdAt: Date;
    updatedAt: Date;
    fileId: string;
    albumArt: string;
    genre: string;
    
    albumArtUrl: string;
}


