import { Component, EventEmitter, Input, Output } from "@angular/core";

import {Track} from "../../models/track.model";
import {StringUtil} from "../../utils/string.util";

import {AppConfig} from "../../configs/app.config";

@Component({
    selector: "list-track",
    template: `
<div class="row">
    <div *ngFor="let item of tracks" class="col-md-6 media track">
        <div class="media-left media-middle">
            <a routerLink="/{{item.nameUnsign}}/{{item.id}}">
            <img class="thumbnail media-object" width="150" height="150" [src]="item.albumArtUrl ? item.albumArtUrl :''" alt="album-art"/>
            </a>
        </div>
            <div class="media-body">
            <h4 class="media-heading"><a routerLink="/{{item.nameUnsign}}/{{item.id}}">{{item.name}}</a></h4>
            <p>{{item.artist}}</p>
            <p>
            <span> <span class="glyphicon glyphicon-time" aria-hidden="true"></span> {{stringUtil.convertTime(item.length)}} </span>
            <span> <span class="glyphicon glyphicon-equalizer" aria-hidden="true"></span> {{item.bitrate}} </span>
            </p>
            <p>
            <span> <span class="glyphicon glyphicon-headphones" aria-hidden="true"></span> {{item.playTime}} </span>
            <span> <span class="glyphicon glyphicon-download" aria-hidden="true"></span> {{item.downloadTime}} </span>
            </p>
            <p *ngIf = "flagShowControls">
            <button [permission]="appConfig.permissions.UPDATE_PLAYLIST" class="btn btn-default btn-info" (click)="invokeAddToPlaylist(item)" title="Add To Playlist"><span class="glyphicon glyphicon-list-alt" aria-hidden="true"></span></button>
            <a [permission]="appConfig.permissions.UPDATE_TRACK" routerLink="/tracks/{{item.nameUnsign}}/{{item.id}}/edit" class="btn btn-default btn-success" role="button" title="Edit"><span class="glyphicon glyphicon-pencil" aria-hidden="true"></span></a>
            <button [permission]="appConfig.permissions.DELETE_TRACK" class="btn btn-default btn-danger" (click)="invokeDeleteItem(item)" title="Delete"><span class="glyphicon glyphicon-trash" aria-hidden="true"></span></button>
            </p>
        </div>
    </div>
</div>
`
})
export class ListTrackComponent {
    @Input("tracks") tracks: Track[];
    @Input("?controls") flagShowControls: boolean = true;
    @Output("onDeleteItem") onDeleteItem: EventEmitter<void> = new EventEmitter<void>();
    @Output("onAddToPlaylist") onAddToPlaylist: EventEmitter<void> = new EventEmitter<void>();
    
    constructor(private appConfig: AppConfig, private stringUtil: StringUtil) {}
    invokeDeleteItem(e: any): void {
        this.onDeleteItem.next(e);
    }
    invokeAddToPlaylist(e: any): void {
        this.onAddToPlaylist.next(e);
    }
}


