import { Component, OnInit } from "@angular/core";
import { Router, ActivatedRoute }       from "@angular/router";
import { Subscription } from "rxjs/Subscription";

import { Playlist } from "../../models/playlist.model";
import { Pagination } from "../../models/pagination.model";
import { PlaylistService } from "../../services/playlist.service";
import { AppConfig } from "../../configs/app.config";
import { PopupService } from "../../services/popup.service";

@Component({
    selector: 'edit-playlist',
    template: `
    <h3>Edit Playlist</h3>
<playlist-form [playlist]="playlist" (doSubmit)="savePlaylist($event)" (doCancel)="goBack($event)"></playlist-form>
`
})
export class EditPlaylistComponent implements OnInit {
    private playlist: Playlist;
    private sub: Subscription;

    constructor(private playlistService: PlaylistService,
        private popupService: PopupService,
        private router: Router,
        private route: ActivatedRoute) {}

    ngOnInit(): void {
        this.playlist = new Playlist();
        this.sub = this.route.params.subscribe(params => {
            let id = params["id"];
            this.playlistService.getOne(id).then(playlist => {
                this.playlist = playlist;
            }).catch((err) => {
                this.popupService.showPopup("Load Playlist", "Can not load playlist info");
            });
        });
    }

    private savePlaylist(): void {
        let playlistDto = new Playlist();
        playlistDto.id = this.playlist.id;
        playlistDto.name = this.playlist.name;
        playlistDto.trackIds = this.playlist.trackIds;
        this.playlistService.save(playlistDto).then(data => {
            console.log(this.playlist);
            this.popupService.showPopup("Save Playlist", "Save playlist success");
            this.goBack();
        }).catch(err => {
            this.popupService.showPopup("Save Playlist", "Save playlist fail");
        });
    }
    
    private goBack(): void {
        this.router.navigate(['/playlists']);
    }
}


