import { Component, EventEmitter, Input, Output } from "@angular/core";

import {Track} from "../../models/track.model";
import {StringUtil} from "../../utils/string.util";

@Component({
    selector: "hits-track",
    template: `
<div class="row">
    <div *ngFor="let track of tracks" class="col-md-6 media track">
        <div class="media-left media-middle">
            <a routerLink="/{{track.nameUnsign}}/{{track.id}}">
                <img class="thumbnail media-object" width="150" height="150" [src]="track.albumArtUrl ? track.albumArtUrl :''" alt="album-art"/>
            </a>
        </div>
        <div class="media-body">
            <h4 class="media-heading"><a routerLink="/{{track.nameUnsign}}/{{track.id}}">{{track.name}}</a></h4>
            <p>{{track.artist}}</p>
            <p>
                <span> <span class="glyphicon glyphicon-time" aria-hidden="true"></span> {{track.length}} </span>
                <span> <span class="glyphicon glyphicon-equalizer" aria-hidden="true"></span> {{track.bitrate}} </span>
            </p>
            <p>
                <span> <span class="glyphicon glyphicon-headphones" aria-hidden="true"></span> {{track.playTime}} </span>
                <span> <span class="glyphicon glyphicon-download" aria-hidden="true"></span> {{track.downloadTime}} </span>
            </p>
            <p [permission]="'MT'">
                <button class="btn btn-default btn-info" (click)="invokeAddToPlaylist(track)" title="Add To Playlist"><span class="glyphicon glyphicon-list-alt" aria-hidden="true"></span></button>
                <a routerLink="/tracks/{{track.nameUnsign}}/{{track.id}}/edit" class="btn btn-default btn-success" role="button" title="Edit"><span class="glyphicon glyphicon-pencil" aria-hidden="true"></span></a>
                <button class="btn btn-default btn-danger" (click)="invokeDeleteItem(track)" title="Delete"><span class="glyphicon glyphicon-trash" aria-hidden="true"></span></button>
            </p>
        </div>
    </div>
</div>
`
    })
export class HitsTrackComponent {
    @Input("tracks") tracks: Track[];
    
constructor(private stringUtil: StringUtil){};
}


