import { Routes, RouterModule } from "@angular/router";

import { DashBoardComponent }   from "./components/common/dash-board.component";
import { ManageTrackComponent, NewTrackComponent }   from "./components/track/manage-track.component";
import { PlayTrackComponent }   from "./components/track/play-track.component";
import { EditTrackComponent }   from "./components/track/edit-track.component";
import { ManagePlaylistComponent }   from "./components/playlist/manage-playlist.component";
import { NewPlaylistComponent }   from "./components/playlist/new-playlist.component";
import { EditPlaylistComponent }   from "./components/playlist/edit-playlist.component";
import { PlayPlaylistComponent }   from "./components/playlist/play-playlist.component";
import { ManageArtistComponent }   from "./components/artist/manage-artist.component";
import { NewArtistComponent }   from "./components/artist/new-artist.component";
import { EditArtistComponent }   from "./components/artist/edit-artist.component";
import { UserLoginComponent }   from "./components/user/user-login.component";
import { ChangePasswordComponent }   from "./components/user/profile.component";

const appRoutes: Routes = [
    {
        path: "",
        //redirectTo: "/dashboard",
        //pathMatch: "full"
        component: DashBoardComponent
    },
    {
        path: "login",
        component: UserLoginComponent
    },
    {
        path: "profile/change-password",
        component: ChangePasswordComponent
    },
    {
        path: "artists",
        component: ManageArtistComponent
    },
    {
        path: "artists/new",
        component: NewArtistComponent
    },
    {
        path: "artists/:nameUsign/:id/edit",
        component: EditArtistComponent
    },
    {
        path: "playlists",
        component: ManagePlaylistComponent
    },
    {
        path: "playlists/new",
        component: NewPlaylistComponent
    },
    {
        path: "playlists/:nameUnsign/:id/edit",
        component: EditPlaylistComponent
    },
    {
        path: "playlists/:nameUnsign/:id",
        component: PlayPlaylistComponent
    },
    {
        path: "tracks",
        component: ManageTrackComponent
    },
    {
        path: "tracks/new",
        component: NewTrackComponent
    },
    {
        path: "tracks/:nameUnsign/:id/edit",
        component: EditTrackComponent
    },
    {
        path: ":nameUnsign/:id",
        component: PlayTrackComponent
    },
    {
        path: "category/edit/:titleUnsign/:id",
        component: DashBoardComponent
    }
];

export const routing = RouterModule.forRoot(appRoutes);


