import { Component, EventEmitter, Input, Output } from "@angular/core";

import {Artist} from "../../models/artist.model";

import {AppConfig} from "../../configs/app.config";

@Component({
    selector: "list-artist",
    template: `
<table class="table">
    <tr>
        <th class="col-sm-1">ID</th>
        <th class="col-sm-3">Name</th>
        <th class="col-sm-3">Name Unsign</th>
        <th class="col-sm-3">Short Info</th>
        <th class="col-sm-2"></th>
    </tr>
    <tr *ngFor="let item of artists">
    <td>{{item.id}}</td>
    <td><a routerLink="/{{item.nameUnsign}}/{{item.id}}">{{item.name}}</a></td>
    <td>{{item.nameUnsign}}</td>
    <td>{{item.shortInfo}}</td>
    <td>
        <a routerLink="/artists/{{item.nameUnsign}}/{{item.id}}" class="btn btn-sm btn-info" role="button">Details</a>
<a routerLink="/artists/{{item.nameUnsign}}/{{item.id}}/edit" class="btn btn-sm btn-success" role="button" [permission]="appConfig.permissions.UPDATE_ARTIST">Edit</a>
<button class="btn btn-sm btn-danger" (click)="invokeDeleteItem(item)" [permission]="appConfig.permissions.DELETE_ARTIST">Delete</button>
        </td>
    </tr>
</table>
`
    })
export class ListArtistComponent {
    @Input("artists") artists: Artist[];
    @Output("onDeleteItem") onDeleteItem: EventEmitter<void> = new EventEmitter<void>();
    constructor(private appConfig: AppConfig) {}
    invokeDeleteItem(e: any): void {
        this.onDeleteItem.next(e);
    }
}


