import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";

import { AppConfig } from "../../configs/app.config";

import { AuthService }  from '../../services/auth.service';
import { PopupService }  from '../../services/popup.service';

@Component({
    selector: "navigation",
    template: `
    <nav class="navbar navbar-inverse">
    <div class="container">
    <div class="navbar-header">
        <button type="button" class="navbar-toggle collapsed" data-toggle="collapse" data-target="#navbar" aria-expanded="false" aria-controls="navbar">
        <span class="sr-only">Toggle navigation</span>
        <span class="icon-bar"></span>
        <span class="icon-bar"></span>
        <span class="icon-bar"></span>
        </button>
        <a class="navbar-brand" href="/" ng-click="brandClicked()">TMusic</a>
    </div>
    <div id="navbar" class="collapse navbar-collapse">
        <ul class="nav navbar-nav">
        <li *ngFor="let item of menuItems" permission="{{item.permission}}" [ngClass]="{'active': item === currentTab}" (click)="changeCurrentTab(item)" >
        <a routerLink="{{item.path}}" routerLinkActive="active">{{item.title}}</a>
        </li>
        </ul>
        <ul class="nav navbar-nav navbar-right">
    <li *ngIf="!authService.isAuthenticated()" [ngClass]="{'active': menuRightItems[0] === currentTab}" (click)="changeCurrentTab(menuRightItems[0])"><a routerLink="{{menuRightItems[0].path}}">{{menuRightItems[0].title}}</a></li>
<li class="dropdown" *ngIf="authService.isAuthenticated()" [ngClass]="{'active': menuRightItems[1] === currentTab}">
    <a href="javascript:void(0);" class="dropdown-toggle" data-toggle="dropdown" role="button" aria-haspopup="true" aria-expanded="false">{{authService.getUsername()}} <span class="caret"></span></a>
                <ul class="dropdown-menu">
    <li><a routerLink="{{menuRightItems[1].path}}" (click)="changeCurrentTab(menuRightItems[1])">Change Password</a></li>
    <li><a href="javascript:void(0);" (click)="logout()">Log Out</a></li>
                </ul>
            </li>
        </ul>
    </div>
    </div>
    </nav>
    `,
    styleUrls: []})

export class NavigationComponent implements OnInit {
    menuItems: Object[];
    menuRightItems: Object[];
    currentTab: Object;
    
constructor(private appConfig: AppConfig, private authService: AuthService, private router: Router, private popupService: PopupService) {}
    
    ngOnInit(): void {
        this.menuItems = [
            {
                title: "Home",
                path: "/",
                permission: this.appConfig.permissions.LOGIN
            },
            {
                title: "Tracks",
                path: "/tracks",
                permission: this.appConfig.permissions.LOGIN
            },
            {
                title: "Artists",
                path: "/artists",
                permission: this.appConfig.permissions.LOGIN
            },
            {
                title: "Playlists",
                path: "/playlists",
                permission: this.appConfig.permissions.LOGIN
            },
            {
                title: "Users",
                path: "/users",
                permission: this.appConfig.permissions.MANAGE_USERS
            }
        ];
        this.menuRightItems = [
            {
                title: "Login",
                path: "/login",
                permission: this.appConfig.permissions.LOGIN
            }, 
            {
                title: "Change Password",
                path: "/profile/change-password",
                permission: this.appConfig.permissions.LOGIN
            }
        ];
        this.currentTab = this.menuItems[0];
    }
    
    changeCurrentTab(menuItem: Object) {
        this.currentTab = menuItem;
    }
    
    logout(): void {
        this.popupService.showPopup("Log Out", "Do you want to log out ?", () => {
            this.authService.logout().then(data => this.router.navigate(['/'])).catch(err => {
                console.log(err);
            });
            this.popupService.hide();
        }, () => this.popupService.hide());
    }
}


