import { Component, EventEmitter, Input, Output, Renderer, OnInit, ElementRef, ViewChild} from "@angular/core";

import { TrackDto } from "../../models/track.dto";
import { Artist } from "../../models/artist.model";
import { Genre } from "../../models/genre.model";

@Component({
    selector: "track-form",
    template: `
<form class="form-horizontal" (ngSubmit)="invokeSubmit()" #myForm="ngForm">
    <div *ngIf="!track.id" class="form-group">
    <div class="col-sm-offset-2 col-sm-10">
        <input type="checkbox" [(ngModel)]="flagGetFile" name="flagGetFile" />
        Get file from URL
    </div>
    </div>
<div *ngIf="!track.id && !flagGetFile" class="form-group" [ngClass]="{'has-error': fileError != null}">
        <label for="file" class="col-sm-2 control-label">Mp3 File</label>
        <div class="col-sm-10">
<input #mp3File name="file" type="file" (change)="checkFile(mp3File)" required/>
            <span class="help-block">
            {{fileError}}
            </span>
        </div>
    </div>
<div *ngIf="!track.id && flagGetFile"  [ngClass]="{'has-error': !(path.valid || path.pristine), 'form-group': true}">
        <label for="path" class="col-sm-2 control-label">Mp3 URL</label>
        <div class="col-sm-10">
            <input type="text" class="form-control" [(ngModel)]="track.path" #path="ngModel" name="path" placeholder="Mp3 URL" required />
            <span class="help-block" [hidden]="path.valid || path.pristine">
            {{ name.required ? "Mp3 URL is required" : ""}}
            </span>
        </div>
    </div>
    <div [ngClass]="{'has-error': !(name.valid || name.pristine), 'form-group': true}">
        <label for="name" class="col-sm-2 control-label">Title</label>
        <div class="col-sm-10">
            <input type="text" class="form-control" [(ngModel)]="track.name" #name="ngModel" name="track-name" placeholder="Name" required />
            <span class="help-block" [hidden]="name.valid || name.pristine">
            {{ name.required ? "Track name is required" : ""}}
            </span>
        </div>
    </div>
    <div class="form-group">
        <label for="author" class="col-sm-2 control-label">Artist</label>
        <div class="col-sm-10">
<select #selectBox1 class="form-control" [(ngModel)]="track.artistIds" name="artists" multiple="true" >
            <option *ngFor="let a of artists" value="{{a.id}}">{{a.name}}</option>
        </select>
        </div>
    </div>
    <div class="form-group">
        <label for="category" class="col-sm-2 control-label">Genre</label>
        <div class="col-sm-10">
<select #selectBox2 class="form-control" [(ngModel)]="track.genreId" name="genre">
                <option *ngFor="let g of genres" value="{{g.id}}">{{g.name}}</option>
            </select>
        </div>
    </div>
    <div class="form-group">
        <div class="col-sm-offset-2 col-sm-10">
        <button type="submit" class="btn btn-success" [disabled]="!myForm.valid">Save</button>
        <button type="reset" class="btn btn-default" (click)="invokeCancel()">Cancel</button>
        </div>
    </div>
</form>
`
})

export class TrackFormComponent implements OnInit {
    private flagGetFile: boolean;
    @Input("track") track: TrackDto = new TrackDto();
    @Input("artists") artists: Artist[];
    @Input("genres") genres: Genre[];
    @Output("doSubmit") doSubmit: EventEmitter<void> = new EventEmitter<void>();
    @Output("doCancel") doCancel: EventEmitter<void> = new EventEmitter<void>();
    
    @ViewChild("selectBox1") viewSelectBox1: ElementRef;
    @ViewChild("selectBox2") viewSelectBox2: ElementRef;
    
    private fileError: string = null;
    
    constructor(private renderer: Renderer) {}
    
    invokeSubmit(e: any): void {
    if (!this.flagGetFile) {
        this.track.path = null;
    }
        this.doSubmit.next(e);
    }
    invokeCancel(e: any): void {
        this.doCancel.next(e);
    }
    
ngOnInit(): void {
    window.setTimeout(() => {
        window["jQuery"](this.viewSelectBox1.nativeElement).select2().on('change', (e: any) => {
            var item, i, n = this.viewSelectBox1.nativeElement.options.length, values = [];
            for(i=0; i< n; i++){
                item = this.viewSelectBox1.nativeElement.options[i];
                if(item.selected){
                    values.push(item.value.split("'")[1]);
                }
            }
            //console.log(values);
            this.track.artistIds = values;
        });
        window["jQuery"](this.viewSelectBox2.nativeElement).select2().on('change', (e: any) => {
            var value = this.viewSelectBox2.nativeElement.value;
            //console.log(value);
            this.track.genreId = value;
        });
    }, 200);
    this.flagGetFile = false;
}
private checkFile(mp3File) {
    let files = mp3File.files;
    if(!files || files.length < 1){
        this.fileError = 'No file selected';
    } else {
        this.track.file = files[0];
    if(this.track.file.type.toLowerCase().indexOf('audio/') === -1){
        this.fileError = 'File is not audio/mp3';
    }
    //check file size
    else if(this.track.file.size > 512 * 1024 * 1024 ){
        this.fileError = 'File is too large. Max size is 512MB';
    }
    else {
        this.fileError = null;
    }
    }
}
}


