import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { User } from '../../models/user.model';

import { AuthService }  from '../../services/auth.service';

@Component({
    selector: "user-login",
    template: `
<h3>Login</h3>
<div [hidden]="!(err.message && err.errors && !err.errors.usename && !err.errors.password)" class="alert alert-danger" role="alert">{{err.message}}</div>
<login [user]="user" [(serverErrors)]="err.errors" (doSubmit)="login($event)" (doCancel)="goBack($event)"></login>
`
    })
export class UserLoginComponent implements OnInit {
    user: User;
    err: any;

    constructor(private authService: AuthService, private router: Router) {}

    ngOnInit(): void {
        this.user = new User();
        this.err = {errors:{}};
    }

    login(): void {
        this.err = {errors:{}};
        this.authService.login(this.user)
            .then(data => {this.authService.storeToken(data) ? this.router.navigate(['/']) : this.err = "Token is invalid";})
        .catch(err => {
            this.err = err.json();
            //this.err.message = "Authentication service is not available right now. Try again.";
            //console.log(err);
        });
    }
    goBack(): void {
        this.router.navigate(['/']);
    }
}


