import { Component, OnInit } from "@angular/core";
import { Router }       from "@angular/router";
import { Subscription } from "rxjs/Subscription";

import { Playlist } from "../../models/playlist.model";
import { PlaylistService } from "../../services/playlist.service";
import { PopupService } from "../../services/popup.service";

@Component({
    selector: 'new-playlist',
    template: `
    <h3>New Playlist</h3>
<playlist-form [playlist]="playlist" (doSubmit)="savePlaylist($event)" (doCancel)="goBack($event)"></playlist-form>
`
})
export class NewPlaylistComponent implements OnInit {
    private playlist: Playlist;
    
    constructor(
        private playlistService: PlaylistService,
        private popupService: PopupService,
        private router: Router) {}

    ngOnInit(): void {
        this.playlist = new Playlist();
    }

    private savePlaylist(): void {
        this.playlistService.save(this.playlist).then(data => {
            this.popupService.showPopup("Save Playlist", "Save playlist success");
        }).catch(err => {
            this.popupService.showPopup("Save Playlist", "Save playlist fail");
        });
    }
    private goBack(): void {
        this.router.navigate(['/playlists']);
    }
}


