import { Component, OnInit, OnDestroy } from "@angular/core";
import { Router, ActivatedRoute }       from "@angular/router";
import { Subscription } from "rxjs/Subscription";

import { Track } from "../../models/track.model";
import { Pagination } from "../../models/pagination.model";
import { TrackService } from "../../services/track.service";

import { Playlist } from "../../models/playlist.model";
import { TrackPlaylistsDto } from "../../models/track-playlists.dto";
import { PlaylistService } from "../../services/playlist.service";
import { ImageService } from "../../services/image.service";

import { AppConfig } from "../../configs/app.config";
import { PopupService } from "../../services/popup.service";
import {StoreUtil} from "../../utils/store.util";

// ====ManageTrack====
@Component({
    selector: "manage-track",
    template: `
<h3>Tracks ({{totalTracks}})</h3>
<p [permission]="appConfig.permissions.CREATE_TRACK"><a routerLink="/tracks/new">New Track</a></p>
<div class="row">
    <div class="col-sm-6">
        <div class="input-group">
            <input type="text" class="form-control" [(ngModel)]="term" name="term" placeholder="Search" (keypress)="enterSearch($event.keyCode)" />
            <div class="input-group-btn">
                <button class="btn btn-success" (click)="searchTrack()">Search</button>
            </div>
        </div>
    </div>
    <div class="col-sm-6">
        <div class="input-group">
            <label for="orderby" class="col-sm-4 control-label">Order by</label>
            <div class="col-sm-4">
                <select class="form-control" [(ngModel)]="orderby" name="orderby" (change)="onChange()">
                    <option value="id">Update time</option>
                    <option value="nameUnsign">Track name</option>
                    <option value="playTime">Play times</option>
                    <option value="downloadTime">Download times</option>
                </select>
            </div>
            <div class="col-sm-4">
                <select class="form-control" [(ngModel)]="direction" name="direction" (change)="onChange()">
                <option value="asc">Ascending</option>
                <option value="desc">Descending</option>
                </select>
            </div>
        </div>
    </div>
</div>
<pagination [pagination]="pagination" (onSelectPage)="loadPage($event)" (onPrePagination)="detectPrePagination($event)" (onNextPagination)="detectNextPagination($event)"></pagination>
<list-track [tracks]="tracks" (onDeleteItem)="deleteTrack($event)" (onAddToPlaylist)="addToPlaylist($event)"></list-track>
<pagination [pagination]="pagination" (onSelectPage)="loadPage($event)" (onPrePagination)="detectPrePagination($event)" (onNextPagination)="detectNextPagination($event)"></pagination>

<div [ngClass]="{'modal fade': true, 'hidden': !flagAddToPlaylist,'in': flagAddToPlaylist, 'show': flagAddToPlaylist}" role="dialog">
<div class="modal-dialog" role="document">
    <div class="modal-content">
    <div class="modal-header">
<button type="button" class="close" aria-label="Close" (click)="cancelAddToPlaylist()"><span aria-hidden="true" (click)="cancelAddToPlaylist()">&times;</span></button>
<h4 class="modal-title">Add To Playlists</h4>
</div>
<div class="modal-body">
<track-to-playlist [model]="trackPlaylistsDto" [myPlaylists]="myPlaylists" (doSubmit)="doAddToPlaylist($event)" (doCancel)="cancelAddToPlaylist()"></track-to-playlist>
</div>
</div>
</div>
</div>
`
})
export class ManageTrackComponent implements OnInit, OnDestroy {
    private tracks: Track[] = [];
    private pagination: Pagination;
    private totalTracks: number;
    private term: string;
    private isPending: boolean;
    private orderby: string;
    private direction: string;
    private page: number;

    private flagAddToPlaylist: boolean;
    private trackPlaylistsDto: TrackPlaylistsDto = new TrackPlaylistsDto();
    private myPlaylists: Playlist[] = [];

    constructor(private trackService: TrackService, private popupService: PopupService, private playlistService: PlaylistService, private appConfig: AppConfig, private imageService: ImageService, private storeUtil: StoreUtil) {
    }

    ngOnInit(): void {
        
        this.term = this.storeUtil.cache.get('track:term');
        this.orderby = this.storeUtil.cache.get('track:orderby') || "id";
        this.direction = this.storeUtil.cache.get('track:direction') || "asc";
        var page = this.storeUtil.cache.get('track:page');
        this.page = page ? +page : 0;
        
        this.pagination = new Pagination();
        this.pagination.pageSize = 20;
        this.pagination.currentPage = -1;
        this.pagination.maxNumOfPages = 7;
        this.pagination.endPage = this.pagination.maxNumOfPages - 1;

        this.loadPage(this.page);
    }
    
    loadPage(page: number, forceLoad: boolean = false) {
        if (this.isPending){
            return;
        }
        this.isPending = true;
        if (forceLoad === true || this.pagination.currentPage !== page) {
                this.trackService.query(page, this.pagination.pageSize, this.term, `${this.orderby}:${this.direction}`).then(hitList => {
                this.page = page;
                this.totalTracks = hitList.total;
                this.tracks = hitList.hits;
                this.pagination.numOfPages = Math.floor(hitList.total / this.pagination.pageSize) + 1;
                if (this.pagination.numOfPages < this.pagination.maxNumOfPages) {
                    this.pagination.endPage = this.pagination.numOfPages - 1;
                }
                this.pagination.currentPage = page;
                this.tracks.forEach((item, i) => {
                    this.imageService.getAlbumArt(item.albumArt, 120, 120).then(src => {
                        item.albumArtUrl = src;
                    });
                });
            }).catch(err => console.log("Load tracks failed", err)).then(() => this.isPending = false);
        }
    }
    
    detectPrePagination() {
        let p = this.pagination.detectPrePagination();
        if (p !== this.pagination) {
            this.pagination = p;
            this.loadPage(this.pagination.currentPage, true);
        }
    }

    detectNextPagination() {
        let p = this.pagination.detectNextPagination();
        if (p !== this.pagination) {
            this.pagination = p;
            this.loadPage(this.pagination.currentPage, true);
        }
    }
    
    deleteTrack(track: Track): void {
        this.popupService.showPopup("Delete Track", "Are you sure you would like to delete \"" + track.name + "\"?",
        () => {
            this.popupService.showPopup("Confirm Delete Track", "Are you sure ?",
            () => {
                this.popupService.showPopup("Confirm Delete Track", "Really ?",
                    () => {
                        //TODO Delete on server
                        let i = this.tracks.indexOf(track);
                        this.tracks.splice(i, 1);
                        this.totalTracks -= 1;
                        this.popupService.showPopup("Delete Track", "Delete success");
                    },
                    () => { this.popupService.hide(); }
                );
                },
            () => { this.popupService.hide(); });
            },
        () => { this.popupService.hide(); });
    }
    
    private searchTrack() {
        if (this.term != null && this.term.trim() != '') {
            this.loadPage(0, true);
        }
    }

    private enterSearch(keyCode: number) {
        if (keyCode == 13) {
            this.searchTrack();
        }
    }
    
    private onChange() {
        //wait for binding
        window.setTimeout(() => this.loadPage(0, true), 100);
    }
    
private addToPlaylist(track: Track) {
        this.trackPlaylistsDto.trackId = '' + track.id;
        this.trackPlaylistsDto.name = track.name;
        this.trackPlaylistsDto.artist = track.artist;
        this.trackPlaylistsDto.playlistIds = [];

        if (this.myPlaylists.length < 1) {
            this.playlistService.listMyPlaylists().then(hitList => {
                this.myPlaylists = hitList.hits;
                this.flagAddToPlaylist = true;
            }).catch(err => {
                this.popupService.showPopup("Add To Playlists", "Load your playlists failed");
            });
        } else {
            this.flagAddToPlaylist = true;
        }
    }
    
    private cancelAddToPlaylist() {
    console.log(this.trackPlaylistsDto);
        this.flagAddToPlaylist = false;
    }

    private doAddToPlaylist() {
        this.playlistService.addTrack(this.trackPlaylistsDto.trackId, this.trackPlaylistsDto.playlistIds).then(response => {
            this.popupService.showPopup("Add To Playlists", "Add track to playlit(s) success");
        }).catch(err => {
            this.popupService.showPopup("Add To Playlists", "Add track to playlit(s) failed");
        }).then(()=> {
            this.cancelAddToPlaylist();
        });
    }
    
    ngOnDestroy() {
        this.storeUtil.cache.set('track:term', this.term);
        this.storeUtil.cache.set('track:orderby', this.orderby);
        this.storeUtil.cache.set('track:direction', this.direction);
        this.storeUtil.cache.set('track:page', '' + this.page);
    }
}

// ====NewTrack====
import { TrackDto } from "../../models/track.dto";
import { Artist } from "../../models/artist.model";
import { Genre } from "../../models/genre.model";
import { ArtistService } from "../../services/artist.service";
import { GenreService } from "../../services/genre.service";

@Component({
    selector: 'new-track',
    template: `
<h3>New Track</h3>
<track-form [track]="track" [artists]="allArtists" [genres]="allGenres" (doSubmit)="saveTrack($event)" (doCancel)="goBack($event)"></track-form>

`
    })
export class NewTrackComponent implements OnInit {
    private track: TrackDto;
    private allArtists: Artist[];
    private allGenres: Genre[];

    constructor(private trackService: TrackService,
        private artistService: ArtistService,
        private genreService: GenreService,
        private popupService: PopupService,
        private router: Router) {
    }

    ngOnInit(): void {
        this.track = new TrackDto();
        this.artistService.listIdName().then(artists => this.allArtists = artists).catch(err => console.log('Get artists fail', err));
        this.genreService.listIdName().then(genres => this.allGenres = genres).catch(err => console.log('Get genres fail', err));
    }

    saveTrack(): void {
    console.log(this.track);
        if (this.track.path != null) {
            this.trackService.saveFromUrl(this.track).then(data => {
                this.popupService.showPopup("Save Track", "Save track success");
            }).catch(err => {
                this.popupService.showPopup("Save Track", "Save track fail");
            });
        } else {
            this.trackService.upload(this.track).then(data => {
                this.popupService.showPopup("Save Track", "Save track success");
            }).catch(err => {
                this.popupService.showPopup("Save Track", "Save track fail");
            });
        }
        
    }
    goBack(): void {
        this.router.navigate(['/tracks']);
    }
}


