import { Injectable }    from "@angular/core";
import { Http, Headers } from "@angular/http";

import { Genre } from "../models/genre.model";
import { HitList } from "../models/hit-list.model";
import { Service } from "./service";
import { AppConfig } from "../configs/app.config";

@Injectable()
export class GenreService extends Service<Genre> {
    constructor(http: Http,private appConfig: AppConfig) {
        super(http, appConfig.serviceApiUrl + "/api/v1/genres/");
    }

    /**
     * List id name
     */
    listIdName(): Promise<Genre[]> {
        let url = this.apiUrl + "list-id-name";
        return this.http.get(url)
            .toPromise()
            .then(response => new HitList<Genre>(JSON.parse(response["_body"])).hits)
            .catch(this.handleError);
    }

}


