import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { User } from '../../models/user.model';

import { AuthService }  from '../../services/auth.service';
import { UserService }  from '../../services/user.service';
import { PopupService }  from '../../services/popup.service';

@Component({
    selector: "change-password",
    template: `
<h3>Change password: {{authService.getUsername()}}</h3>
<div [hidden]="!err" class="alert alert-danger" role="alert">{{err}}</div>
<form class="form-horizontal" (ngSubmit)="changePassword()" #myForm="ngForm">
    <div [ngClass]="{'has-error': !(curPassword.valid || curPassword.pristine), 'form-group': true}">
    <label for="curPassword0" class="col-sm-2 control-label">Current Password</label>
    <div class="col-sm-10">
    <input type="password" class="form-control" [(ngModel)]="user.curPassword" #curPassword="ngModel" name="curPassword0" placeholder="Password" required />
    <span class="help-block" [hidden]="curPassword.valid || curPassword.pristine">
    {{ curPassword.errors ? curPassword.errors.required ? "Password is required" : "" : ""}}
    </span>
    </div>
    </div>
    <div [ngClass]="{'has-error': !(password.valid || password.pristine), 'form-group': true}">
    <label for="password0" class="col-sm-2 control-label">New Password</label>
    <div class="col-sm-10">
<input type="password" class="form-control" [(ngModel)]="user.password" #password="ngModel" name="password0" placeholder="New Password" required minlength="5" maxlength="32"/>
    <span class="help-block" [hidden]="password.valid || password.pristine">
    {{ password.errors ? 
        password.errors.required ? 'New password is required' :
        password.errors.minlength ? 'Min length is 5' :
        password.errors.maxlength ? 'Max length is 32' :
'' : ''}}
    </span>
    </div>
    </div>
    <div [ngClass]="{'has-error': !(confirmPassword.valid || confirmPassword.pristine), 'form-group': true}">
    <label for="confirmPassword0" class="col-sm-2 control-label">Confirm Password</label>
    <div class="col-sm-10">
<input type="password" class="form-control" [(ngModel)]="user.confirmPassword" #confirmPassword="ngModel" name="confirmPassword0" placeholder="Confirm Password" required pattern="{{user.password}}" />
    <span class="help-block" [hidden]="confirmPassword.valid || confirmPassword.pristine">
    {{ confirmPassword.errors ? 
        confirmPassword.errors.required ? 'Confirm password is required' :
        confirmPassword.errors.pattern ? 'Confirm password is not matched' :
'' : ''}}
    </span>
    </div>
    </div>
    <div class="form-group">
    <div class="col-sm-offset-2 col-sm-10">
    <button type="submit" class="btn btn-success" [disabled]="!myForm.valid">Submit</button>
    <button type="reset" class="btn btn-default" >Cancel</button>
    </div>
    </div>
</form>
`
    })
export class ChangePasswordComponent implements OnInit {
    user: User;
    err: any;

constructor(private authService: AuthService, private router: Router, private popupService: PopupService, private userService: UserService) {
    }

    ngOnInit(): void {
        this.user = new User();
        this.user.username = this.authService.getUsername();
    }

    changePassword(): void {
        this.userService.changePassword(this.user).then(data => {
            this.popupService.showPopup("Change password", "Change password success");
        })
        .catch(err => {
            this.popupService.showPopup("Change password", "Change password fail. " + err["_body"]);
        });
    }
}


