import { Injectable }    from "@angular/core";
import { Http, Headers } from "@angular/http";

import { AppConfig } from "../configs/app.config";

@Injectable()
export class ImageService {
constructor(private appConfig: AppConfig, private http: Http) {
    }
    
    private myCache: Map<string, string> = new Map<string, string>();
    
    /**
     * Resize image by google sevice proxy
     * @param path
     * @param width
     * @param height
     * @param timeToLive - time (in second) to cache at server, default 10 days
     */
    public resizeImage(path: string, width?: number, height?: number, timeToLive?: number): string {
        let url : string = `https:\/\/images1-focus-opensocial.googleusercontent.com\/gadgets\/proxy?url=${path}&container=focus${width ? "&resize_w=" + width : ""}${height ? "&resize_h=" + height : ""}${timeToLive ? "&refresh=" + timeToLive : "&refresh=846000"}`;
        return url;
    }
    
    public getAlbumArt(albumArtId: string, width?: number, height?: number): Promise<string> {
        if(this.myCache.has(albumArtId)) {
            let url = this.myCache.get(albumArtId);
            return Promise.resolve(this.resizeImage(url, width, height));
        }
        let fileId = albumArtId ? albumArtId : this.appConfig.defaultAlbumArtId;
        return this.http.get(`${this.appConfig.serviceApiUrl}/api/v1/files/redirect-link?fileId=${fileId}`)
            .toPromise().then(response => {
                let data = JSON.parse(response["_body"]);
                this.myCache.set(albumArtId, data["redirectUrl"]);
                return this.resizeImage(data["redirectUrl"], width, height);
            }).catch(err => "");
    }

}


