import { Injectable }    from "@angular/core";
import { Http, Headers } from "@angular/http";

import { HitList } from "../models/hit-list.model";
import { Track } from "../models/track.model";
import { Service } from "./service";
import { AppConfig } from "../configs/app.config";

import { TrackDto } from "../models/track.dto";

@Injectable()
export class TrackService extends Service<Track> {
    //cache tracks src
    private myCache: Map<string, string> = new Map<string, string>();

    constructor(http: Http,private appConfig: AppConfig) {
        super(http, appConfig.serviceApiUrl + "/api/v1/tracks/");
    }

    /**
     * Query track
     * @param {number} page
     * @param {number} size
     * @param {string} q - srearch term
     * @param {string} order - field1:asc,field2:desc,...
     */
    query(page: number = 0, size?: number, q?: string, order?: string): Promise<HitList<Track>> {
        let url = this.apiUrl + "?page=" + page;
        if(typeof size === "number") {
            url += "&size=" + size;
        }
        if (typeof q === "string") {
            url += "&q=" + q;
        }
        if (typeof order === "string") {
            url += "&order=" + order;
        }
        return this.http.get(url)
        .toPromise()
        .then(response => {
            let data = JSON.parse(response["_body"]);
            return new HitList<Track>(data);
        })
        .catch(this.handleError);
    }

    /**
     * Get track source by file id
     * @param {string} fileId - google file id
     */
    getTrackSource(fileId: string): Promise<string> {
        if(this.myCache.has(fileId)) {
            return Promise.resolve(this.myCache.get(fileId));
        }
        return this.http.get(`${this.appConfig.serviceApiUrl}/api/v1/files/redirect-link?url=https:\/\/drive.google.com\/uc?id=${fileId}&export=download`)
            .toPromise()
            .then(response => {
                let data = JSON.parse(response["_body"]);
                this.myCache.set(fileId, data["redirectUrl"]);
                    return data["redirectUrl"];
                })
            .catch(this.handleError);
    }

    upview(id: number): Promise<void> {
        let headers = new Headers();
        headers.append("Content-Type", "application/json");
        return this.http.put(this.apiUrl + id + "/upview", "", {headers: headers})
            .toPromise()
            .then(response => response.json().data)
            .catch(this.handleError);
    }

    upload(trackDto: TrackDto): Promise<Object> {
        let headers = new Headers();
        let fd = new FormData();
        fd.append('file', trackDto.file);
        fd.append('name', trackDto.name);
        fd.append('artistIds', trackDto.artistIds);
        fd.append('genreId', trackDto.genreId);
        fd.append('lyric', trackDto.lyric);
        return this.http
            .post(this.apiUrl + "upload", fd, {headers: headers})
            .toPromise()
            .then(res => res.json().data)
            .catch(this.handleError);
    }

    saveFromUrl(trackDto: TrackDto): Promise<Object> {
        let headers = new Headers({"Content-Type": "application/json"});
        headers.append("XToken", localStorage.getItem("token"));
        return this.http
            .post(this.apiUrl + "fetch-from-url", JSON.stringify(trackDto), {headers: headers})
            .toPromise()
            .then(res => res.json().data)
        .catch(this.handleError);
    }

    update(trackDto: TrackDto): Promise<Object> {
        let headers = new Headers({"Content-Type": "application/json"});
        headers.append("XToken", localStorage.getItem("token"));
        return this.http
            .put(this.apiUrl, JSON.stringify(trackDto), {headers: headers})
            .toPromise()
            .then(res => res.json().data)
        .catch(this.handleError);
    }
}


