import { Component, EventEmitter, Input, Output } from "@angular/core";

import {Playlist} from "../../models/playlist.model";

import {AppConfig} from "../../configs/app.config";

@Component({
    selector: "list-playlist",
    template: `
    <table class="table">
    <tr>
    <th class="col-sm-1 text-center">ID</th>
    <th class="col-sm-3 text-center">Name</th>
    <th class="col-sm-2 text-center">Play Times</th>
    <th class="col-sm-1 text-center">Create Date</th>
    <th class="col-sm-1 text-center">Update</th>
    <th class="col-sm-2 text-center">Username</th>
    <th class="col-sm-2"></th>
    </tr>
    <tr *ngFor="let item of playlists">
    <td class="text-center">{{item.id}}</td>
    <td><a routerLink="/playlists/{{item.nameUnsign}}/{{item.id}}">{{item.name}}</a></td>
    <td class="text-center">{{item.playTime}}</td>
    <td class="text-center">{{item.createdAt}}</td>
    <td  class="text-center">{{item.updatedAt}}</td>
    <td  class="text-center">{{item.username}}</td>
    <td>
<a routerLink="/playlists/{{item.nameUnsign}}/{{item.id}}" class="btn btn-sm btn-info" role="button">Details</a>
<a routerLink="/playlists/{{item.nameUnsign}}/{{item.id}}/edit" class="btn btn-sm btn-success" role="button" >Edit</a>
<button class="btn btn-sm btn-danger" (click)="invokeDeleteItem(item)" [permission]="appConfig.permissions.DELETE_PLAYLIST">Delete</button>
</td>
</tr>
</table>
`
    })
export class ListPlaylistComponent {
    @Input("playlists") playlists: Playlist[];
    @Output("onDeleteItem") onDeleteItem: EventEmitter<void> = new EventEmitter<void>();
constructor(private appConfig: AppConfig) {}
    invokeDeleteItem(e: any): void {
        this.onDeleteItem.next(e);
    }
}


