import { Component, OnInit } from "@angular/core";

import { Playlist } from "../../models/playlist.model";
import { Pagination } from "../../models/pagination.model";
import { PlaylistService } from "../../services/playlist.service";
import { AppConfig } from "../../configs/app.config";
import { PopupService } from "../../services/popup.service";

@Component({
    selector: "manage-playlist",
    template: `
    <h3>Playlists</h3>
    <p [permission]="appConfig.permissions.CREATE_PLAYLIST"><a routerLink="/playlists/new">New Playlist</a></p>
<pagination [pagination]="pagination" (onSelectPage)="loadPage($event)" (onPrePagination)="detectPrePagination($event)" (onNextPagination)="detectNextPagination($event)"></pagination>
<list-playlist [playlists]="playlists" (onDeleteItem)="deletePlaylist($event)"></list-playlist>
<pagination [pagination]="pagination" (onSelectPage)="loadPage($event)" (onPrePagination)="detectPrePagination($event)" (onNextPagination)="detectNextPagination($event)"></pagination>
`
    })
export class ManagePlaylistComponent implements OnInit {
    playlists: Playlist[] = [];
    pagination: Pagination;

    constructor(
        private playlistService: PlaylistService,
        private popupService: PopupService,
        private appConfig: AppConfig) {}

    ngOnInit(): void {
        this.pagination = new Pagination();
        this.pagination.pageSize = 20;
        this.pagination.currentPage = -1;
        this.pagination.maxNumOfPages = 7;
        this.pagination.endPage = this.pagination.maxNumOfPages - 1;

        this.loadPage(0);
    }

    loadPage(page: number, forceLoad: boolean = false) {
        if (forceLoad === true || this.pagination.currentPage !== page) {
            this.playlistService.query(page, this.pagination.pageSize).then(hitList => {
                this.playlists = hitList.hits;
                this.pagination.numOfPages = Math.floor(hitList.total / this.pagination.pageSize) + 1;
                if (this.pagination.numOfPages < this.pagination.maxNumOfPages) {
                    this.pagination.endPage = this.pagination.numOfPages - 1;
                }
                this.pagination.currentPage = page;
            }).catch(err => console.log("Load playlists failed", err));
        }
    }

    detectPrePagination() {
        let p = this.pagination.detectPrePagination();
        if (p !== this.pagination) {
            this.pagination = p;
            this.loadPage(this.pagination.currentPage, true);
        }
    }

    detectNextPagination() {
        let p = this.pagination.detectNextPagination();
        if (p !== this.pagination) {
            this.pagination = p;
            this.loadPage(this.pagination.currentPage, true);
        }
    }

    deletePlaylist(playlist: Playlist): void {
        // TODO
        this.popupService.showPopup("Delete Playlist", "Can not delete because the function is not ready");
        console.log("Delete playlist", playlist);
    }
}


