import { Component, EventEmitter, Input, Output, Renderer, OnInit, ElementRef, ViewChild} from "@angular/core";

import { Artist } from "../../models/artist.model";

@Component({
    selector: "artist-form",
    template: `
<form class="form-horizontal" (ngSubmit)="invokeSubmit()" #myForm="ngForm">
<div [ngClass]="{'has-error': !(name.valid || name.pristine), 'form-group': true}">
    <label for="name" class="col-sm-2 control-label">Name</label>
    <div class="col-sm-10">
    <input type="text" class="form-control" [(ngModel)]="artist.name" #name="ngModel" name="name" placeholder="Name" required />
    <span class="help-block" [hidden]="name.valid || name.pristine">
    {{ name.required ? "Artist name is required" : ""}}
    </span>
    </div>
</div>
<div class="form-group">
<label for="short-info" class="col-sm-2 control-label">Short Info</label>
<div class="col-sm-10">
<textarea class="form-control" [(ngModel)]="artist.shortInfo" name="short-info" rows="6" ></textarea>
</div>
</div>
<div class="form-group">
<div class="col-sm-offset-2 col-sm-10">
<button type="submit" class="btn btn-success" [disabled]="!myForm.valid">Save</button>
<button type="reset" class="btn btn-default" (click)="invokeCancel()">Cancel</button>
</div>
</div>
</form>
`
})

export class ArtistFormComponent {
    @Input("artist") artist: Artist = new Artist();
    @Output("doSubmit") doSubmit: EventEmitter<void> = new EventEmitter<void>();
    @Output("doCancel") doCancel: EventEmitter<void> = new EventEmitter<void>();

    constructor(private renderer: Renderer) {}
invokeSubmit(e: any): void {
        this.doSubmit.next(e);
    }
invokeCancel(e: any): void {
        this.doCancel.next(e);
    }

}


