import { Injectable }    from "@angular/core";
import { Http, Headers } from "@angular/http";

import { HitList } from "../models/hit-list.model";
import { Playlist } from "../models/playlist.model";
import { Service } from "./service";
import { AppConfig } from "../configs/app.config";
import { AuthService } from "./auth.service";

@Injectable()
export class PlaylistService extends Service<Playlist> {
constructor(http: Http,private appConfig: AppConfig, private authService: AuthService) {
        super(http, appConfig.serviceApiUrl + "/api/v1/playlists/");
    }
    
    /**
     * Query playlist
     * @param {number} page
     * @param {number} size
     * @param {string} q - srearch term
     * @param {string} order - field:direction
     */
    query(page: number = 0, size?: number, q?: string, order?: string): Promise<HitList<Playlist>> {
        let url = this.apiUrl + "?page=" + page;
        if  (typeof size === "number") {
            url += "&size=" + size;
        }
        if (typeof q === "string") {
            url += "&q=" + q;
        }
        if (typeof order === "string") {
            url += "&order=" + order;
        }
        return this.http.get(url)
                .toPromise()
                .then(response => {
                    let data = JSON.parse(response["_body"]);
                    return new HitList<Playlist>(data);
                })
                .catch(this.handleError);
    }

    listMyPlaylists() : Promise<HitList<Playlist>> {
        //TO DO
        return this.query(0, 100);
    }
    
    addTrack(trackId: string, playlistIds: string[]): Promise<Object> {
        let headers = new Headers({"Content-Type": "application/json"});
        headers.append("XToken", this.authService.getToken());
        let jData = {
            "trackId": trackId + "",
            "playlistIds": playlistIds
        }
        return this.http
        .post(this.apiUrl + "add-track", JSON.stringify(jData), {headers: headers})
        .toPromise()
        .then(res => res.json().data)
            .catch(this.handleError);
    }
    
    upview(id: number): Promise<void> {
        let headers = new Headers();
        headers.append("Content-Type", "application/json");
        return this.http.post(this.apiUrl + id + "/upview", "", {headers: headers})
            .toPromise()
            .then(response => response.json().data)
            .catch(this.handleError);
    }
}


