import { Component, EventEmitter, Input, Output, OnInit } from "@angular/core";

import { Pagination } from "../../models/pagination.model";

@Component({
    selector: "pagination",
    template: `
<nav aria-label="Page navigation">
<ul class="pagination">
<li *ngIf="pagination.startPage > 0">
<a href="javascript:void(0)" aria-label="Previous" (click)="invokePrePagination()">
<span aria-hidden="true">&laquo;</span>
</a>
</li>
<li *ngFor="let i of pagination.generatePageIds()" [ngClass]="{'active': pagination.currentPage === i}"><a href="javascript:void(0)" (click)="invokeSelectPage(i)">{{i+1}}</a></li>
<li *ngIf="pagination.endPage < pagination.numOfPages - 1">
<a href="javascript:void(0)" aria-label="Next" (click)="invokeNextPagination()">
<span aria-hidden="true">&raquo;</span>
</a>
</li>
</ul>
</nav>
`
})
export class PaginationComponent {
    @Input("pagination") pagination: Pagination;
    @Output("onSelectPage") onSelectPage: EventEmitter<void> = new EventEmitter<void>();
    @Output("onPrePagination") onPrePagination: EventEmitter<void> = new EventEmitter<void>();
    @Output("onNextPagination") onNextPagination: EventEmitter<void> = new EventEmitter<void>();
    
    invokeSelectPage(e: any): void {
        this.onSelectPage.next(e);
    }
    
    invokePrePagination(e: any): void {
        this.onPrePagination.next(e);
    }
    
    invokeNextPagination(e: any): void {
        this.onNextPagination.next(e);
    }
}


