import { Component, EventEmitter, Input, Output, Renderer, OnInit, ElementRef, ViewChild} from "@angular/core";

import { Playlist } from "../../models/playlist.model";
import { Track } from "../../models/track.model";

import {StringUtil} from "../../utils/string.util";

@Component({
    selector: "playlist-form",
    template: `
<form class="form-horizontal" (ngSubmit)="invokeSubmit()" #myForm="ngForm">
    <div [ngClass]="{'has-error': !(name.valid || name.pristine), 'form-group': true}">
    <label for="name" class="col-sm-2 control-label">Name</label>
    <div class="col-sm-10">
    <input type="text" class="form-control" [(ngModel)]="playlist.name" #name="ngModel" name="name" placeholder="Name" required />
    <span class="help-block" [hidden]="name.valid || name.pristine">
    {{ name.required ? "Playlist name is required" : ""}}
    </span>
    </div>
    </div>
    <div class="form-group">
    <label for="short-info" class="col-sm-2 control-label">Tracks</label>
    <div class="col-sm-10">
        <ul #sortableList class="list-group">
            <li *ngFor="let item of playlist.tracks; let i = index" data-id="{{item.id}}" class="list-group-item" data-toggle="tooltip" title="{{item.name + '<br/>' + item.artist}}">
                <button type="button" class="float-right btn-xs btn-danger" (click)="removeTrack(item)">
                    <span class="glyphicon glyphicon-remove"></span>
                </button>
                <span class="track-title">{{i + 1}}. {{item.name }}</span>
                <br/> <span class="track-artist"> {{item.artist}}</span>
                <span class="badge">{{stringUtil.convertTime(item.length)}}</span>
            </li>
        </ul>
    </div>
    </div>
    <div class="form-group">
    <div class="col-sm-offset-2 col-sm-10">
    <button type="submit" class="btn btn-success" [disabled]="!myForm.valid">Save</button>
    <button type="reset" class="btn btn-default" (click)="invokeCancel()">Cancel</button>
    </div>
    </div>
</form>
`
})

export class PlaylistFormComponent implements OnInit {
    @Input("playlist") playlist: Playlist = new Playlist();
    @Output("doSubmit") doSubmit: EventEmitter<void> = new EventEmitter<void>();
    @Output("doCancel") doCancel: EventEmitter<void> = new EventEmitter<void>();
    
    @ViewChild("sortableList") viewSortableList: ElementRef;

constructor(private renderer: Renderer, private stringUtil: StringUtil) {}
    ngOnInit() {
        window["jQuery"](this.viewSortableList.nativeElement).sortable({
            placeholder: "ui-state-highlight"
        });
        window["jQuery"](this.viewSortableList.nativeElement).disableSelection();
    }

private removeTrack(track: Track) {
        let i = this.playlist.tracks.indexOf(track);
        if (i >= 0) {
            this.playlist.tracks.splice(i, 1);
        }
    }

    private invokeSubmit(e: any): void {
        let items = this.viewSortableList.nativeElement.getElementsByTagName("li"),
            i = 0,
            n = items.length;
        this.playlist.trackIds = [];
        for(; i < n; i++) {
            this.playlist.trackIds.push(items[i].getAttribute("data-id") || items[i].getAttribute("id"));
        }
        this.doSubmit.next(e);
    }

    private invokeCancel(e: any): void {
        this.doCancel.next(e);
    }
}


