import { Component, EventEmitter, Input, Output } from "@angular/core";

import { User } from "../../models/user.model";

@Component({
    selector: "login",
    template: `
<form class="form-horizontal" (ngSubmit)="invokeSubmit()" #myForm="ngForm">
    <div [ngClass]="{'has-error': !(username.valid || username.pristine) || serverErrors.username, 'form-group': true}">
        <label for="username" class="col-sm-2 control-label">Username</label>
        <div class="col-sm-10">
            <input type="text" class="form-control" [(ngModel)]="user.username" name="username" #username="ngModel" placeholder="Username"
            required
            minlength="5" maxlength="32"
            />
            <span class="help-block" [hidden]="username.valid || username.pristine">
                {{ username.errors ? 
                    username.errors.required ? 'Username is required' :
                    username.errors.minlength ? 'Min length is 5' :
                    username.errors.maxlength ? 'Max length is 32' :
                    '' : ''
                }}
            </span>
            <span class="help-block" [hidden]="!serverErrors.username">
                {{ serverErrors.username }}
            </span>
        </div>
    </div>
    <div [ngClass]="{'has-error': !(password.valid || password.pristine) || serverErrors.password, 'form-group': true}">
        <label for="password" class="col-sm-2 control-label">Password</label>
        <div class="col-sm-10">
            <input type="password" class="form-control" [(ngModel)]="user.password" name="password" #password="ngModel" placeholder="Password"
            required
            minlength="5" maxlength="32"
            />
            <span class="help-block" [hidden]="password.valid || password.pristine">
                {{ password.errors ? 
                    password.errors.required ? 'Password is required' :
                    password.errors.minlength ? 'Min length is 5' :
                    password.errors.maxlength ? 'Max length is 32' :
                    '' : ''
                }}
            </span>
            <span class="help-block" [hidden]="!serverErrors.password">
                {{ serverErrors.password }}
            </span>
        </div>
    </div>
    <div class="form-group">
        <div class="col-sm-offset-2 col-sm-10">
            <button type="submit" class="btn btn-success" [disabled]="!myForm.valid">Login</button>
            <button type="reset" class="btn btn-default" (click)="invokeCancel()">Cancel</button>
        </div>
    </div>
</form>
`
})

export class LoginComponent {
    @Input("user") user: User = new User();
    @Input("serverErrors") serverErrors: Object = {};
    @Output("doSubmit") doSubmit: EventEmitter<void> = new EventEmitter<void>();
    @Output("doCancel") doCancel: EventEmitter<void> = new EventEmitter<void>();
invokeSubmit(e: any): void {
    this.doSubmit.next(e);
}
invokeCancel(e: any): void {
    this.doCancel.next(e);
}
}


