import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { Subscription } from "rxjs/Subscription";

import { Artist } from "../../models/artist.model";
import { ArtistService } from "../../services/artist.service";
import { PopupService } from "../../services/popup.service";

@Component({
    selector: 'new-artist',
    template: `
    <h3>New Artist</h3>
<artist-form [artist]="artist" (doSubmit)="saveArtist($event)" (doCancel)="goBack($event)"></artist-form>
`
})
export class NewArtistComponent implements OnInit {
    private artist: Artist;
    
    constructor(private artistService: ArtistService,
        private popupService: PopupService,
        private router: Router) {}

    ngOnInit(): void {
        this.artist = new Artist();
    }

    private saveArtist(): void {
        this.artistService.save(this.artist).then(data => {
            this.popupService.showPopup("Save Artist", "Save artist success");
        }).catch(err => {
            this.popupService.showPopup("Save Artist", "Save artist fail");
        });
    }
    
    private goBack(): void {
        this.router.navigate(['/artists']);
    }
}


