import { Injectable }    from "@angular/core";
import { Http, Headers } from "@angular/http";

import { Artist } from "../models/artist.model";
import { Service } from "./service";
import { AppConfig } from "../configs/app.config";
import { HitList } from "../models/hit-list.model";

@Injectable()
export class ArtistService extends Service<Artist> {
    constructor(http: Http,private appConfig: AppConfig) {
        super(http, appConfig.serviceApiUrl + "/api/v1/artists/");
    }

    /**
     * List id name
     */
    listIdName(): Promise<Artist[]> {
        let url = this.apiUrl + "list-id-name";
        return this.http.get(url)
            .toPromise()
            .then(response => {
                let data = JSON.parse(response["_body"]);
                return new HitList<Artist>(data).hits
            }).catch(this.handleError);
    }

    /**
     * Query artist
     * @param {number} page
     * @param {number} pageSize
     * @param {string} q - srearch term
     * @param {string} order - field:asc,field2:desc
     */
    query(page: number = 0, size?: number, q?: string, order?: string): Promise<HitList<Artist>> {
        let url = this.apiUrl + "?page=" + page;
        if  (typeof size === "number") {
            url += "&size=" + size;
        }
        if (typeof q === "string") {
            url += "&q=" + q;
        }
        if (typeof order === "string") {
            url += "&order=" + order;
        }
        return this.http.get(url)
            .toPromise()
            .then(response => {
            let data = JSON.parse(response["_body"]);
            return new HitList<Artist>(data);
//            let hitList = new HitList<Artist>(data);
//                hitList.total = data.length;
//                hitList.count = data.length;
//            for(let i = 0; i < hitList.count; i++) {
//                let item = data[i];
//                hitList.hits.push(item as Artist);
//            }
//            return hitList;
            })
            .catch(this.handleError);
    }
}


