import {Injectable} from "@angular/core";

import {Track} from "../models/track.model";
import {TrackService} from "./track.service";
import {ImageService} from "./image.service";

@Injectable()
export class AudioPlayer {
    
    //keep audio player data
    track: Track = new Track();
    arrTrack: Track[] = [];
    trackIndex: number = 0;
    shouldShowPersistent: boolean = false;
    playlistId: number;
    returnPath: string;
    private fnOnEnded : EventListener = null;

    private elemAudio: HTMLAudioElement;
    private flagLoop: boolean = true;
    private elemCanvas: HTMLElement;

    constructor(
        private trackService: TrackService,
        private imageService: ImageService) {
        this.elemAudio = document.createElement("audio");
    }

    public setAttributes(attibutes: Object) {
        for (let key in attibutes) {
            this.elemAudio[key] = attibutes[key];
        }
    }

    public setSource(src: string) {
        this.elemAudio["src"] = src;
    }

    public play(): void {
        if(!this.isPlaying()) {
            this.elemAudio["play"]();
        }
    }

    public pause(): void {
        if(this.isPlaying()) {
            this.elemAudio["pause"]();
        }
    }

    public stop(): void {
        this.pause();
        this.setSource("");
    }

    public onTimeUpdate(fnOnTimeUpdate: EventListener): EventListener {
        this.elemAudio.addEventListener("timeupdate", fnOnTimeUpdate);
        return fnOnTimeUpdate;
    }

    public onPlay(fnOnplay: EventListener): EventListener {
        this.elemAudio.addEventListener("play", fnOnplay);
        return fnOnplay;
    }

    public onPause(fnOnPause: EventListener): EventListener {
        this.elemAudio.addEventListener("pause", fnOnPause);
        return fnOnPause;
    }
    
    public onEnded(fnOnEnded: EventListener): EventListener {
        this.elemAudio.addEventListener("ended", fnOnEnded);
        return fnOnEnded;
    }
    
    public removeOnTimeUpdate(fnOnTimeUpdate: EventListener): EventListener {
        this.elemAudio.removeEventListener("timeupdate", fnOnTimeUpdate);
            return fnOnTimeUpdate;
    }

    public removeOnPlay(fnOnplay: EventListener): EventListener {
        this.elemAudio.removeEventListener("play", fnOnplay);
            return fnOnplay;
    }

    public removeOnPause(fnOnPause: EventListener): EventListener {
        this.elemAudio.removeEventListener("pause", fnOnPause);
            return fnOnPause;
    }
    
    public removeOnEnded(fnOnEnded: EventListener): EventListener {
        this.elemAudio.removeEventListener("ended", fnOnEnded);
        return fnOnEnded;
    }

    public getCurrentTime(): number {
            return this.elemAudio["currentTime"];
    }

    public getDuration(): number {
        return this.elemAudio["duration"];
    }
    
    public seek(currentTime: number): void {
        if (currentTime < 0 || currentTime > this.getDuration()) {
            return;
        }
        this.elemAudio["currentTime"] = currentTime;
    }

    public isPlaying(): boolean {
            return !this.elemAudio["paused"];
    }
    
    public getSource(): string {
            return this.elemAudio["src"];
    }
    
    public isLoop(): boolean {
        return this.flagLoop;
    }
    
    public setLoop(loop: boolean): void {
        this.flagLoop = loop;
    }
    
    public setVolume(volume: number): void {
        if (volume < 0 || volume > 1) {
            return;
        }
        this.elemAudio.volume = volume;
    }
    
    public getVolume(): number {
        return this.elemAudio.volume;
    }
    
    public setMuted(muted: boolean): void {
        this.elemAudio.muted = muted;
    }
    
    public isMuted(): boolean {
        return this.elemAudio.muted;
    }
    
    
    public playTrack(index: number): Promise<void> {
        if (index < 0 || index >= this.arrTrack.length) {
            return Promise.reject(`Index is oversize: ${index} in ${this.arrTrack.length}`);
        }
        let item = this.arrTrack[index];
        return this.trackService.getOne(item.id + "").then(track => {
            this.track = track;
            this.trackIndex = index;
            this.setAttributes({"type": "audio/mp3"});
            this.trackService.getTrackSource(track.fileId).then(src => {
                this.setAttributes({"crossOrigin": "anonymous"});
                this.setSource(src);
                //render visulization
            }).catch(err => {
                this.setAttributes({"crossOrigin": undefined});
                this.setSource(`https:\/\/drive.google.com\/uc?id=${this.track.fileId}&export=download`);
            }).then(() => {
                this.play();
   
                //Event audio ended -> play next track
                if (this.fnOnEnded == null) {
                    this.fnOnEnded = this.onEnded(() => {
                        if (this.trackIndex < this.arrTrack.length - 1) {
                            this.playTrack(this.trackIndex + 1);
                        } else {
                            if (this.isLoop()) {
                                if (this.arrTrack.length > 1) {
                                    this.playTrack(0);
                                } else {
                                    this.seek(0);
                                    this.play();
                                    this.trackService.upview(track.id);
                                }
                            } else {
                                this.pause();
                            }
                        }
                    });
                }
            });

            //get album art source
            this.imageService.getAlbumArt(track.albumArt, 150, 150).then(src => {
                this.track.albumArt = src;
            }).then(() => {
                this.trackService.upview(track.id); 
            });
        }).catch(err => {
            console.log("Load track failed");
            throw err;
        });
    }

    public getAudioVisualation(): HTMLElement {
        if (this.elemCanvas == null) {
            this.elemCanvas = document.createElement("canvas");
            this.elemCanvas.setAttribute("width", "800");
            this.elemCanvas.setAttribute("height", "280");
            this.renderAudioVisualation(this.elemCanvas);
        }
        return this.elemCanvas;
    }
    //Audio Visualation
    public renderAudioVisualation(canvas: HTMLElement, width: number = 800, height: number = 250): void {
        window["AudioContext"] = window["AudioContext"] || window["webkitAudioContext"] || window["mozAudioContext"];

        let ctx = new window["AudioContext"]();
        let analyser = ctx.createAnalyser();
        let audioSrc = ctx.createMediaElementSource(this.elemAudio);
        // we have to connect the MediaElementSource with the analyser 
        audioSrc.connect(analyser);
        analyser.connect(ctx.destination);
        // we could configure the analyser: e.g. analyser.fftSize (for further infos read the spec)
        // analyser.fftSize = 64;
        // frequencyBinCount tells you how many values you"ll receive from the analyser
        let frequencyData = new Uint8Array(analyser.frequencyBinCount);

        // we"re ready to receive some data!
        let cWidth = canvas["width"],
        cHeight = canvas["height"] - 2,
        meterWidth = 10, //width of the meters in the spectrum
        gap = 2, //gap between meters
        capHeight = 2,
        capStyle = "#fff",
        meterNum = cWidth / (10 + 2), //count of the meters
        capYPositionArray = []; ////store the vertical position of hte caps for the preivous frame
        ctx = canvas["getContext"]("2d");
        let gradient = ctx.createLinearGradient(0, 0, 0, cHeight);
        gradient.addColorStop(1, "#0f0");
        gradient.addColorStop(0.5, "#ff0");
        gradient.addColorStop(0, "#f00");
        
        let renderFrame = function() {
            let array = new Uint8Array(analyser.frequencyBinCount);
            analyser.getByteFrequencyData(array);
            let step = Math.round(array.length / meterNum); //sample limited data from the total array
            ctx.clearRect(0, 0, cWidth, cHeight);
            for (let i = 0; i < meterNum; i++) {
                let value = array[i * step];
                if (capYPositionArray.length < Math.round(meterNum)) {
                    capYPositionArray.push(value);
                };
                ctx.fillStyle = capStyle;
                //draw the cap, with transition effect
                if (value < capYPositionArray[i]) {
                    ctx.fillRect(i * 12, cHeight - (--capYPositionArray[i]), meterWidth, capHeight);
                } else {
                    ctx.fillRect(i * 12, cHeight - value, meterWidth, capHeight);
                    capYPositionArray[i] = value;
                };
                ctx.fillStyle = gradient; //set the filllStyle to gradient for a better look
                ctx.fillRect(i * 12 /*meterWidth+gap*/ , cHeight - value + capHeight, meterWidth, cHeight); //the meter
            }
            requestAnimationFrame(renderFrame);
        }

        renderFrame();
    }
}


