import { Component, EventEmitter, Input, Output, Renderer, OnInit, ElementRef, ViewChild} from "@angular/core";

import { Playlist } from "../../models/playlist.model";
import { TrackPlaylistsDto } from "../../models/track-playlists.dto";

@Component({
    selector: "track-to-playlist",
    template: `
<form class="form-horizontal" (ngSubmit)="invokeSubmit()" #myForm="ngForm">
<div class="form-group">
<label for="name" class="col-sm-2 control-label">Title</label>
<div class="col-sm-10">
{{model.name}}
</div>
</div>
<div class="form-group">
<label for="artists" class="col-sm-2 control-label">Artist</label>
<div class="col-sm-10">
{{model.artist}}
</div>
</div>
<div class="form-group">
<label for="playlists" class="col-sm-2 control-label">Select Playlists</label>
<div class="col-sm-10">
<select #selectBox class="form-control" [(ngModel)]="model.playlistIds" name="playlistIds" multiple="true">
<option *ngFor="let p of myPlaylists" value="{{p.id}}">{{p.name}}</option>
</select>
</div>
</div>
<div class="form-group">
<div class="col-sm-offset-2 col-sm-10">
<button type="submit" class="btn btn-success" [disabled]="!myForm.valid">Save</button>
<button type="reset" class="btn btn-default" (click)="invokeCancel()">Cancel</button>
</div>
</div>
</form>
`
})

export class TrackToPlaylistComponent implements OnInit {
    @Input("model") model: TrackPlaylistsDto;
    @Input("myPlaylists") myPlaylists: Playlist[];
    
    @Output("doSubmit") doSubmit: EventEmitter<void> = new EventEmitter<void>();
    @Output("doCancel") doCancel: EventEmitter<void> = new EventEmitter<void>();

    @ViewChild("selectBox") viewSelectBox: ElementRef;

constructor(private renderer: Renderer) {}

private invokeSubmit(e: any): void {
    this.doSubmit.next(e);
}
private invokeCancel(e: any): void {
    this.doCancel.next(e);
}

ngOnInit(): void {
    //window["jQuery"](this.viewSelectBox.nativeElement).select2();
}

}


