import { Component, OnInit, OnDestroy, ElementRef, ViewChild, Renderer } from "@angular/core";
import { Router, ActivatedRoute }       from "@angular/router";
import { Subscription } from "rxjs/Subscription";

import {Track} from "../../models/track.model";
import {TrackService} from "../../services/track.service";
import {ImageService} from "../../services/image.service";
import {AudioPlayer} from "../../services/audio-player.service";

import {StringUtil} from "../../utils/string.util";

@Component({
    selector: "play-track",
    template: `
<div class="row">
    <div class="col-sm-9">
    <div #myCanvas class="audio-visualation"></div>
        <div #audioPayer class="audio-player">
    <img [ngClass]="{'album-art-circle album-art': true, 'rotate-z': audioPlayer.isPlaying()}" [src]="audioPlayer.track.albumArt ? audioPlayer.track.albumArt : ''"/>
            <div class="track-info">
            <div class="track-name">{{audioPlayer.track.name}}</div>
            <div class="track-artist">{{audioPlayer.track.artist}}</div>
            </div>
            <div #progressBar class="play-progress-bar">
                <div #progress class="play-progress">
                    <div class="pointer"></div>
                </div>
            </div>
            <div class="time-duration">
                <div #timeSpan class="time-span"></div>
                <div #timeRemain class="time-remain"></div>
            </div>
            <div #btnLoop class="btn-loop"></div>
            <div #btnPlay class="btn-play"></div>
            <div #btnFastForward class="btn-fast-forward"></div>
            <div #btnRewind class="btn-rewind"></div>
            <div #btnVolume class="btn-volume"></div>
            <div #volumeSlider class="volume-slider hidden">
                <input #volumeSliderBar type="range" min="0" max="1.0" value="{{this.audioPlayer.getVolume()}}" step="0.1" />
            </div>
        </div>
    </div>
</div>
<div class="row">
    <h3>Relative tracks</h3>
<list-track [tracks]="relativeTracks" [?controls]="false"></list-track>
</div>
`
    })
export class PlayTrackComponent implements OnInit, OnDestroy {
    private isAudioVisualationRendered: boolean = false;
    private sub: Subscription;
    
    @ViewChild("audioPlayer") viewAudioPlayer: ElementRef;
    @ViewChild("myCanvas") viewMyCanvas: ElementRef;
    @ViewChild("progressBar") viewProgressBar: ElementRef;
    @ViewChild("progress") viewProgress: ElementRef;
    @ViewChild("timeSpan") viewTimeSpan: ElementRef;
    @ViewChild("timeRemain") viewTimeRemain: ElementRef;
    @ViewChild("btnPlay") viewBtnPlay: ElementRef;
    @ViewChild("btnLoop") viewBtnLoop: ElementRef;
    @ViewChild("btnFastForward") viewBtnFastForward: ElementRef;
    @ViewChild("btnRewind") viewBtnRewind: ElementRef;
    @ViewChild("btnVolume") viewBtnVolume: ElementRef;
    @ViewChild("volumeSlider") viewVolumeSlider: ElementRef;
    @ViewChild("volumeSliderBar") viewVolumeSliderBar: ElementRef;
    
    private fnOnTimeUpdate : EventListener = null;
    private fnOnPlay : EventListener = null;
    private fnOnPause : EventListener = null;
    private fnOnEnded : EventListener = null;
    
    private relativeTracks: Track[];

    constructor(
        private route: ActivatedRoute,
        private router: Router,
        private trackService: TrackService,
        private audioPlayer: AudioPlayer,
        private stringUtil: StringUtil,
        private el: ElementRef,
        private renderer: Renderer,
        private imageService: ImageService
    ) {}

    ngOnInit() {
        this.audioPlayer.shouldShowPersistent = false;
        
        this.sub = this.route.params.subscribe(params => {
            let id = params["id"];
            if (this.audioPlayer.track.id == id) {
                this.viewBtnPlay.nativeElement.classList.remove('btn-play');
                this.viewBtnPlay.nativeElement.classList.add('btn-pause');
                this.viewMyCanvas.nativeElement.appendChild(this.audioPlayer.getAudioVisualation());
                this.registerEvents();
            } else
            this.trackService.getOne(id).then(track => {
                this.audioPlayer.returnPath = `/${track.nameUnsign}/${track.id}`;
                this.audioPlayer.arrTrack = [track];
                this.audioPlayer.playTrack(0).then(() => {
                    this.viewBtnPlay.nativeElement.classList.remove('btn-play');
                    this.viewBtnPlay.nativeElement.classList.add('btn-pause');
                    this.viewMyCanvas.nativeElement.appendChild(this.audioPlayer.getAudioVisualation());
                    this.registerEvents();
                });
            }).catch(err => console.log("Play track failed", err));
        });

        //Play/Pause
        this.renderer.listen(this.viewBtnPlay.nativeElement, "click", (event: any) => {
            if (this.audioPlayer.isPlaying()) {
                this.viewBtnPlay.nativeElement.classList.remove('btn-pause');
                this.viewBtnPlay.nativeElement.classList.add('btn-play');
                this.audioPlayer.pause();
            } else {
                this.viewBtnPlay.nativeElement.classList.remove('btn-play');
                this.viewBtnPlay.nativeElement.classList.add('btn-pause');
                this.audioPlayer.play();
            }
        });

        //Loop or not
        this.renderer.listen(this.viewBtnLoop.nativeElement, "click", (event: any) => {
            if (this.audioPlayer.isLoop()) {
                this.audioPlayer.setLoop(false);
                this.viewBtnLoop.nativeElement.classList.add('disabled');
            } else {
                this.audioPlayer.setLoop(true);
                this.viewBtnLoop.nativeElement.classList.remove('disabled');
            }
        });

        //Volume
        this.renderer.listen(this.viewBtnVolume.nativeElement, "click", (event: any) => {
            if (this.viewVolumeSlider.nativeElement.classList.contains('hidden')) {
                this.viewVolumeSlider.nativeElement.classList.remove('hidden');
            } else {
                this.viewVolumeSlider.nativeElement.classList.add('hidden');
            }
        });
        
        this.renderer.listen(this.viewVolumeSliderBar.nativeElement, "change", (event: any) => {
            let volume = this.viewVolumeSliderBar.nativeElement.value;
            this.audioPlayer.setVolume(volume);
            if (volume == 0) {
                this.viewBtnVolume.nativeElement.classList.add('muted');
            } else {
                this.viewBtnVolume.nativeElement.classList.remove('muted');
            }
        });
        
        //Audio seek
        this.renderer.listen(this.viewProgressBar.nativeElement, "click", (event: any) => {
            let progress = event.offsetX / this.viewProgressBar.nativeElement.offsetWidth;
            let duration = this.audioPlayer.getDuration();
            let currentTime = progress * duration;
            this.audioPlayer.seek(currentTime);
        });
        //Fast forward
        this.renderer.listen(this.viewBtnFastForward.nativeElement, "click", (event: any) => {
            let duration = this.audioPlayer.getDuration();
            let currentTime = this.audioPlayer.getCurrentTime() + 0.02 * duration;
            this.audioPlayer.seek(currentTime);
        });
        //Rewind
        this.renderer.listen(this.viewBtnRewind.nativeElement, "click", (event: any) => {
            let duration = this.audioPlayer.getDuration();
            let currentTime = this.audioPlayer.getCurrentTime() - 0.02 * duration;
            this.audioPlayer.seek(currentTime);
        });
    }
    
    private registerEvents() {
        //Show progress
        if (this.fnOnTimeUpdate == null) {
        this.fnOnTimeUpdate = this.audioPlayer.onTimeUpdate(() => {
            let currentTime = this.audioPlayer.getCurrentTime();
            let duration = this.audioPlayer.getDuration();
            let progress = Math.floor(currentTime * 10000 / duration) / 100;
        if (progress < 98) {
            this.viewProgress.nativeElement.style.width = progress + "%";
        }
            this.viewTimeSpan.nativeElement.innerHTML = this.stringUtil.convertTime(Math.floor(currentTime));
            this.viewTimeRemain.nativeElement.innerHTML = this.stringUtil.convertTime(Math.floor(duration - currentTime));
            });
        }

        //Audio ended
        if (this.fnOnEnded == null) {
            this.fnOnEnded = this.audioPlayer.onEnded(() => {
                this.viewBtnPlay.nativeElement.classList.remove('btn-pause');
                this.viewBtnPlay.nativeElement.classList.add('btn-play');
            });
        }
        if (this.fnOnPause == null) {
            this.fnOnPause = this.audioPlayer.onPause(() => {
                this.viewBtnPlay.nativeElement.classList.remove('btn-pause');
                this.viewBtnPlay.nativeElement.classList.add('btn-play');
            });
        }
        if (this.fnOnPlay == null) {
            this.fnOnPlay = this.audioPlayer.onPlay(() => {
                this.viewBtnPlay.nativeElement.classList.remove('btn-play');
                this.viewBtnPlay.nativeElement.classList.add('btn-pause');

                this.loadRelativeTrack(this.audioPlayer.track);
            });
        }

    }

    ngOnDestroy() {
        if (this.audioPlayer.isPlaying()) {
            this.audioPlayer.shouldShowPersistent = true;
        } else {
            this.audioPlayer.shouldShowPersistent = false;
        }
        
        this.sub.unsubscribe();
        if (this.fnOnTimeUpdate !== null) {
            this.audioPlayer.removeOnTimeUpdate(this.fnOnTimeUpdate);
        }
        if (this.fnOnEnded !== null) {
            this.audioPlayer.removeOnEnded(this.fnOnEnded);
        }
        if (this.fnOnPause !== null) {
            this.audioPlayer.removeOnEnded(this.fnOnPause);
        }
        if (this.fnOnPlay !== null) {
            this.audioPlayer.removeOnEnded(this.fnOnPlay);
        }
    }
    
    private loadRelativeTrack(track: Track) {
        this.trackService.query(0, 10, this.stringUtil.convertoUnsignKeepSpace(track.artist.split('-')[0].trim()), 'playTime:desc').then(hitList => {
            this.relativeTracks = hitList.hits;
            this.relativeTracks.forEach((item, i) => {
            this.imageService.getAlbumArt(item.albumArt, 120, 120).then(src => item.albumArtUrl = src);
            });
        }).catch(err => console.log("Load relative tracks failed", err));
    }

}


