import {Entity} from "./entity.model";

import {Track} from "./track.model";

export class Playlist extends Entity {
    createdAt: string;
    updatedAt: string;
    name: string;
    nameUnsign: string;
    picture: string;
    playTime: number;
    username: string;
    tracksOrderRule: string;
    tracks: Array<Track>;
    trackIds: Array<number>;
}


